/\n\nfieldset {\n  border: 1px solid #c0c0c0;\n  margin: 0 2px;\n  padding: 0.35em 0.625em 0.75em;\n}\n\n/**\n * 1. Correct `color` not being inherited in IE 8/9/10/11.\n * 2. Remove padding so people aren't caught out if they zero out fieldsets.\n */\n\nlegend {\n  border: 0;\n  /* 1 */\n  padding: 0;\n  /* 2 */\n}\n\n/**\n * Remove default vertical scrollbar in IE 8/9/10/11.\n */\n\ntextarea {\n  overflow: auto;\n}\n\n/**\n * Don't inherit the `font-weight` (applied by a rule above).\n * NOTE: the default cannot safely be changed in Chrome and Safari on OS X.\n */\n\noptgroup {\n  font-weight: bold;\n}\n\n/* Tables\n   ========================================================================== */\n\n/**\n * Remove most spacing between table cells.\n */\n\ntable {\n  border-collapse: collapse;\n  border-spacing: 0;\n}\n\ntd,\nth {\n  padding: 0;\n}\n\n/* End Normalize\n   ========================================================================== */\n*,\n*:after,\n*:before {\n  box-sizing: border-box;\n}\n\n*[href] {\n  cursor: pointer;\n}\n","// Sizes\n// --------------------------------------------------\n$header-height: 55px;\n// $header-height-px: rem-to-px($header-height);\n// Colors\n// --------------------------------------------------\n$black: #000000;\n$white: #ffffff;\n$grey: #999999;\n$off-white: #f4f4f4;\n$light: #d9dadc;\n$navy: #0d2d42;\n$pulse: #3bafda;\n// New grays below are being added as placeholder.\n// Usages of similar shades will be replaced in App before removing similar legacy variables.\n$sand: #f4f4f4;\n$silver: #e2e2e2;\n$stone: #bebebe;\n$ash: #a0a0a0;\n$slate: #707070;\n$charcoal: #282828;\n\n// Analytics Colors\n$aqua: #3bafda;\n$ocean: #4a89dc;\n$mint: #37bc9b;\n$grass: #8cc152;\n$sunflower: #f6b042;\n$bittersweet: #eb6845;\n$grapefruit: #da4453;\n$carnation: #d770ad;\n$lavender: #967adc;\n$mountain: #9678b6;\n// Entity Colors\n$company: #3399dd;\n$candidate: #44bb77;\n$lead: #aa6699;\n$contact: #ffaa44;\n$opportunity: #662255;\n$job: 