     &:nth-of-type(odd) {\n        fill: lighten($dark, 5%);\n      }\n      transform-origin: center center;\n      @for $i from 1 through 5 {\n        &:nth-of-type(#{$i}) {\n          //animation: circle#{$i} 3400ms infinite;\n          animation-delay: $i * 70ms;\n        }\n      }\n    }\n\n    &.multicolor {\n      .circleGroup circle {\n        @include enumerate($dot-colors, 'fill', 'map-val', '');\n      }\n    }\n\n    &.white,\n    &[inverse] {\n      .circleGroup circle {\n        fill: white;\n\n        &:nth-of-type(odd) {\n          fill: darken(white, 5%);\n        }\n      }\n    }\n  }\n  @keyframes spin {\n    0% {\n      transform: rotate(0deg);\n    }\n\n    75% {\n      transform: rotate(1360deg);\n    }\n\n    100% {\n      transform: rotate(1444deg);\n    }\n  }\n  @keyframes circle1 {\n    0% {\n      cx: 43;\n      cy: 43;\n    }\n\n    25% {\n      cx: 54;\n      cy: 45;\n    }\n\n    75% {\n      cx: 80;\n      cy: 40;\n    }\n\n    100% {\n      cx: 43;\n      cy: 43;\n    }\n  }\n  @keyframes circle2 {\n    0% {\n      cx: 43;\n      cy: 43;\n    }\n\n    25% {\n      cx: 48;\n      cy: 41;\n    }\n\n    75% {\n      cx: 48;\n      cy: 18;\n    }\n\n    100% {\n      cx: 43;\n      cy: 43;\n    }\n  }\n  @keyframes circle3 {\n    0% {\n      cx: 43;\n      cy: 43;\n    }\n\n    25% {\n      cx: 42;\n      cy: 45;\n    }\n\n    75% {\n      cx: 15;\n      cy: 40;\n    }\n\n    100% {\n      cx: 43;\n      cy: 43;\n    }\n  }\n  @keyframes circle4 {\n    0% {\n      cx: 43;\n      cy: 43;\n    }\n\n    25% {\n      cx: 44;\n      cy: 52;\n    }\n\n    75% {\n      cx: 29;\n      cy: 78;\n    }\n\n    100% {\n      cx: 43;\n      cy: 43;\n    }\n  }\n  @keyframes circle5 {\n    0% {\n      cx: 43;\n      cy: 43;\n    }\n\n    25% {\n      cx: 52;\n      cy: 52;\n    }\n\n    75% {\n      cx: 68;\n      cy: 78;\n    }\n\n    100% {\n      cx: 43;\n      cy: 43;\n    }\n  }\n}\n","novo-select {\n  display: block;\n  position: relative;\n  w